@import "../components/variables";
@import "../components/mixin";

body {
  .busket {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      margin-bottom: 100px;
      display: grid;
      grid-template-areas: "breadcrumb breadcrumb"
                           "busket order";
      grid-template-columns: 65% 35%;
      .content__busket__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__busket{
        grid-area: busket;
        margin-right: 25px;
        h5 {
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          padding: 20px;
          border: 2px solid #e6e6e6;
          border-radius: 15px;
          li{
            margin: 40px 0;
            div{
              display: flex;
              justify-content: space-between;
              align-items: center;
              .value{
                width: 45%;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                text-decoration: none!important;
                &:hover{
                  text-decoration: underline!important;
                }
              }
              .quantity{
                span{
                  display: inline-block;
                  width: 25px;
                  height: 40px;
                  font: 1.375em/40px @sansregular;
                  text-align: center;
                  color: @linkColor;
                  background-color: #77c9cc;
                  user-select: none;
                  &:hover{
                    background-color: @mainColor;
                    cursor: pointer;
                    transition: .5s;
                  }
                }
                .minus{
                  border-top-left-radius: 5px;
                  border-bottom-left-radius: 5px;
                }
                .plus{
                  border-top-right-radius: 5px;
                  border-bottom-right-radius: 5px;
                }
                input{
                  width: 35px;
                  height: 34px;
                  font-size: 1em;
                  font-family: @sansregular;
                  text-align: center;
                  color: @linkColor;
                  border: 2px solid #77c9cc;
                  outline: none;
                }
              }
              .price{
                font: 1.5em/1.625em @sansbold;
                color: @linkColor;
                span{
                  font: .625em/.75em @sansregular;
                }
              }
              .delete{
                display: inline-block;
                font-size: 1.5em;
                color: @mainColor;
                &:hover{
                  cursor: pointer;
                }
              }
            }
            &:first-child{
              margin: 0 0 40px;
            }
            &:last-child{
              margin: 40px 0 0;
            }
          }
        }
        .total{
          margin-top: 25px;
          display: flex;
          h6{
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
          }
          p{
            margin-left: 50px;
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
            span{
              font: .75em/1.125em @sansregular;
            }
          }
        }
        .costumerInfo{
          margin-top: 60px;
          display: flex;
          flex-wrap: wrap;
          flex-direction: column;
          form {
            p {
              width: 265px;
              position: relative;
              display: inline-flex;
              margin: 0 5px;
              input {
                padding: 0 10px;
                width: 250px;
                height: 40px;
                font: 1em/1.125em @serifregular;
                color: #828282;
                outline: none;
                border: 2px solid #e6e6e6;
                border-radius: 5px;
              }
              .asterisk {
                position: absolute;
                top: 20px;
                right: 20px;
                font-size: .35em;
                color: #ff0000;
              }
            }
          }
          textarea{
            margin: 10px 0 30px 5px;
            padding: 10px;
            width: 514px;
            height: 90px;
            font: 1em/1.125em @serifregular;
            color: #828282;
            resize: none;
            border: 2px solid #e6e6e6;
            border-radius: 5px;
            outline: none;
          }
          button{
            width: 250px;
            height: 40px;
            font: .875em/40px @sansregular;
            color: #fff;
            text-align: center;
            text-transform: uppercase;
            background-color: @mainColor;
            border: none;
            border-radius: 5px;
            outline: none;
            &:hover{
              cursor: pointer;
            }
          }
          .error{
            background-color: @errorColor;
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
        margin: 95px 0;
        flex-direction: column;
        div{
          margin: 0 0 50px 0;
          width: 100%;
        }
      }
    }
  }
  #thanksOrder{
    margin: 0 auto;
    padding: 15px 0;
    width: 700px;
    min-height: 100px;
    position: relative;
    text-align: center;
    background: #fff;
    border-radius: 5px;
    .icon-cancel {
      width: 100%;
      text-align: right;
      color: @mainColor;
    }
    #thanksOrderClose {
      width: 21px;
      height: 21px;
      position: absolute;
      top: 10px;
      right: 10px;
      cursor: pointer;
      display: block;
    }
    p{
      margin: 25px 0;
      font: 1.125em/1.25em @sansregular;
      color: #474747;
    }
    button{
      width: 250px;
      height: 40px;
      font: .875em/40px @sansregular;
      color: #fff;
      text-align: center;
      text-transform: uppercase;
      background-color: @mainColor;
      border: none;
      border-radius: 5px;
      outline: none;
      &:hover{
        cursor: pointer;
      }
    }
  }
}